html {
  --secondary-color: #00acdc;
  font-family: Roboto, Arial, sans-serif;
  font-size: 12px;
}

body {
  margin: 0;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__link {
  color: #000;
  text-transform: uppercase;
  height: 60px;
  line-height: 60px;
  text-decoration: none;
  display: block;
}

.nav__item {
  margin-right: 20px;
}

.nav__item:last-of-type {
  margin: 0;
}

.nav__link:hover {
  color: var(--secondary-color);
}

.logo__img {
  width: 40px;
  height: 40px;
  display: block;
}

.is-active {
  color: var(--secondary-color);
  position: relative;
}

.is-active:after {
  content: "";
  background-color: var(--secondary-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}
